import React, { useReducer } from "react";
import { useRouter } from "next/router";
import Link from "next/link";
import cx from "classnames";
import {
  Collapse,
  Navbar,
  NavbarToggler,
  NavbarBrand,
  Nav,
  NavItem,
  NavLink,
} from "reactstrap";

import ExternalLink from "shared/components/ExternalLink";

import styles from "./index.module.css";

const ActiveLink = ({ href, children, containment = false }) => {
  const router = useRouter();
  const pathname = router.pathname;
  const child = React.Children.only(children);

  return (
    <Link href={href}>
      {React.cloneElement(children, {
        className: cx(child.props.className, {
          active: containment ? pathname.includes(href, 0) : pathname === href,
        }),
      })}
    </Link>
  );
};

const Navigation = () => {
  const [open, toggleOpen] = useReducer((state) => !state, false);
  const router = useRouter();

  return (
    <Navbar className="container-md" color="secondary" dark expand="md">
      <NavbarBrand className={styles.brand} onClick={() => router.push("/")}>
        Sushantdhiman.com
      </NavbarBrand>
      <NavbarToggler onClick={toggleOpen} />
      <Collapse isOpen={open} navbar>
        <Nav className={cx(styles.navbar, "mr-auto")} navbar>
          <NavItem>
            <ActiveLink href="/">
              <NavLink>Home</NavLink>
            </ActiveLink>
          </NavItem>
          <NavItem>
            <ActiveLink containment href="/blog">
              <NavLink>Blog</NavLink>
            </ActiveLink>
          </NavItem>
          <NavItem>
            <ActiveLink href="/projects">
              <NavLink>Projects</NavLink>
            </ActiveLink>
          </NavItem>
          <NavItem>
            <ActiveLink href="/about">
              <NavLink>About</NavLink>
            </ActiveLink>
          </NavItem>
        </Nav>
        <Nav className={cx(styles.navbar, "ml-auto flex-row")} navbar>
          <NavItem>
            <ExternalLink
              className="px-2 nav-link"
              href="mailto:sushantdhiman@outlook.com"
              icon={["fas", "envelope"]}
            />
          </NavItem>
          <NavItem>
            <ExternalLink
              className="px-2 nav-link"
              href="https://linkedin.com/in/sushantdhiman"
              icon={["fab", "linkedin"]}
            />
          </NavItem>
          <NavItem>
            <ExternalLink
              className="px-2 nav-link"
              href="https://gitlab.com/sushantdhiman"
              icon={["fab", "gitlab"]}
            />
          </NavItem>
          <NavItem>
            <ExternalLink
              className="px-2 nav-link"
              href="https://github.com/sushantdhiman"
              icon={["fab", "github"]}
            />
          </NavItem>
        </Nav>
      </Collapse>
    </Navbar>
  );
};

export default Navigation;
